interface AutocompletePosition {
  top: number
  left: number
}

interface ShipWithScore {
  ship: Ship
  score: number
}

// Debounce hook
const useDebounce = (value: string, delay: number): string => {
  const [debouncedValue, setDebouncedValue] = useState(value)

  useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedValue(value)
    }, delay)

    return () => {
      clearTimeout(handler)
    }
  }, [value, delay])

  return debouncedValue
}

// Enhanced fuzzy search that works with partial ship names
const fuzzySearch = (query: string, ships: Ship[]): Ship[] => {
  if (!query.trim() || query.length < 2) return []

  const searchTerm = query.toLowerCase()

  return ships
    .map((ship): ShipWithScore | null => {
      const name = ship.ship_name.toLowerCase()
      const words = name.split(' ')

      // Exact match gets highest score
      if (name === searchTerm) return { ship, score: 100 }

      // Full word starts with search term
      const wordMatch = words.find(word => word.startsWith(searchTerm))
      if (wordMatch) return { ship, score: 90 }

      // Name starts with search term
      if (name.startsWith(searchTerm)) return { ship, score: 85 }

      // Name contains search term
      if (name.includes(searchTerm)) return { ship, score: 80 }

      // Class or category match
      const class_ = ship.ship_class?.toLowerCase() || ''
      const category = ship.ship_category?.toLowerCase() || ''
      if (class_.includes(searchTerm) || category.includes(searchTerm)) {
        return { ship, score: 70 }
      }

      // Fuzzy character matching
      let score = 0
      let searchIndex = 0

      for (let i = 0; i < name.length && searchIndex < searchTerm.length; i++) {
        if (name[i] === searchTerm[searchIndex]) {
          score += 1
          searchIndex++
        }
      }

      // If we matched all characters, calculate fuzzy score
      if (searchIndex === searchTerm.length) {
        const fuzzyScore = (score / name.length) * 60
        return { ship, score: fuzzyScore }
      }

      return null
    })
    .filter((item): item is ShipWithScore => item !== null)
    .sort((a, b) => b.score - a.score)
    .slice(0, 8) // Show max 8 results
    .map(item => item.ship)
}

// Function to detect if user is typing a potential ship name
const detectPotentialShipName = (text: string, cursorPosition: number): { word: string, startPos: number, endPos: number } | null => {
  // Get the word at cursor position
  const beforeCursor = text.substring(0, cursorPosition)
  const afterCursor = text.substring(cursorPosition)

  // Find word boundaries (space, comma, period, etc.)
  const wordBoundaries = /[\s,.\-()]/

  // Find start of current word
  let startPos = cursorPosition - 1
  while (startPos >= 0 && !wordBoundaries.test(beforeCursor[startPos])) {
    startPos--
  }
  startPos++

  // Find end of current word
  let endPos = 0
  while (endPos < afterCursor.length && !wordBoundaries.test(afterCursor[endPos])) {
    endPos++
  }
  endPos = cursorPosition + endPos

  const currentWord = text.substring(startPos, endPos)

  // Only show autocomplete if word is at least 2 characters and looks like it could be a ship name
  if (currentWord.length >= 2) {
    // Check if it's likely a ship name (starts with capital, contains INS, HMS, USS, etc.)
    const shipPrefixes = /^(ins|hms|uss|mv|ss)/i
    const isCapitalized = /^[A-Z]/.test(currentWord)
    const isLikelyShipName = shipPrefixes.test(currentWord) || isCapitalized

    if (isLikelyShipName) {
      return { word: currentWord, startPos, endPos }
    }
  }

  return null
}

// Autocomplete Dropdown Component (now dropup)
interface AutocompleteDropdownProps {
  show: boolean
  ships: Ship[]
  position: AutocompletePosition
  selectedIndex: number
  onSelect: (ship: Ship) => void
  onMouseEnter: (index: number) => void
  forwardRef: React.RefObject<HTMLDivElement>
}

function AutocompleteDropdown({
  show,
  ships,
  position,
  selectedIndex,
  onSelect,
  onMouseEnter,
  forwardRef
}: AutocompleteDropdownProps) {
  if (!show || ships.length === 0) return null

  return (
    <div
      ref={forwardRef}
      className="absolute z-50 bg-white border border-gray-200 rounded-lg shadow-lg max-h-60 overflow-y-auto min-w-80"
      style={{
        bottom: `${window.innerHeight - position.top + 8}px`, // Position above the input
        left: `${position.left}px`
      }}
    >
      <div className="p-2">
        <div className="text-xs font-medium text-gray-500 mb-2 px-2">
          Ships ({ships.length})
        </div>
        {ships.map((ship, index) => (
          <div
            key={ship.ship_id}
            className={`px-3 py-2 rounded-md cursor-pointer flex flex-col gap-1 ${index === selectedIndex
                ? 'bg-blue-50 text-blue-900 border border-blue-200'
                : 'hover:bg-gray-50'
              }`}
            onClick={() => onSelect(ship)}
            onMouseEnter={() => onMouseEnter(index)}
          >
            <div className="font-medium text-sm">{ship.ship_name}</div>
            <div className="text-xs text-gray-500">
              {ship.ship_class} â€¢ {ship.ship_category} â€¢ {ship.command}
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

// Message Component
function Message({ message }: { message: Message }) {
  return (
    <div className={`flex gap-4 ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}>
      {message.role === 'assistant' && (
        <div className="w-8 h-8 rounded-full bg-blue-100 flex items-center justify-center mt-1">
          <Bot className="w-4 h-4 text-blue-600" />
        </div>
      )}

      <div className={`max-w-3xl ${message.role === 'user' ? 'order-first' : ''}`}>
        <div className={`rounded-lg p-4 ${message.role === 'user'
            ? 'bg-blue-600 text-white ml-auto'
            : message.isError
              ? 'bg-red-50 text-red-800 border border-red-200'
              : 'bg-gray-100 text-gray-800'
          }`}>
          <div className="whitespace-pre-wrap">{message.content}</div>
        </div>

        <div className="text-xs text-gray-500 mt-1 px-4">
          {new Date(message.timestamp).toLocaleTimeString()}
        </div>
      </div>

      {message.role === 'user' && (
        <div className="w-8 h-8 rounded-full bg-gray-200 flex items-center justify-center mt-1">
          <User className="w-4 h-4 text-gray-600" />
        </div>
      )}
    </div>
  )
}

