// Main Chat Component
export default function ChatMain({ ships = [] }: { ships?: Ship[] }) {
  // Consolidated state
  const [chatState, setChatState] = useState<ChatState>({
    messages: [],
    isLoading: false,
    error: null
  })

  // Input and autocomplete state
  const [inputValue, setInputValue] = useState("")
  const [searchQuery, setSearchQuery] = useState("")
  const [showAutocomplete, setShowAutocomplete] = useState(false)
  const [autocompletePosition, setAutocompletePosition] = useState<AutocompletePosition>({ top: 0, left: 0 })
  const [selectedIndex, setSelectedIndex] = useState(-1)
  const [currentWordBounds, setCurrentWordBounds] = useState<{ startPos: number, endPos: number } | null>(null)

  const inputRef = useRef<HTMLInputElement>(null)
  const autocompleteRef = useRef<HTMLDivElement>(null)

  // Debounced search query
  const debouncedSearchQuery = useDebounce(searchQuery, 100)

  // Memoized filtered ships with fuzzy search
  const filteredShips = useMemo(() => {
    return fuzzySearch(debouncedSearchQuery, ships)
  }, [debouncedSearchQuery, ships])

  // Send message function
  const sendMessage = async () => {
    if (!inputValue.trim() || chatState.isLoading) return

    const userMessage: Message = {
      role: "user",
      content: inputValue.trim(),
      timestamp: new Date().toISOString()
    }

    setChatState(prev => ({
      ...prev,
      messages: [...prev.messages, userMessage],
      isLoading: true,
      error: null
    }))

    const messageToSend = inputValue.trim()
    setInputValue("")
    setShowAutocomplete(false)

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000))

      const assistantMessage: Message = {
        role: "assistant",
        content: `I received your message: "${messageToSend}"\n\nThis is a demo response. In the real implementation, this would process your request and provide relevant information about ship components, hierarchy, or reliability analysis.`,
        timestamp: new Date().toISOString()
      }

      setChatState(prev => ({
        ...prev,
        messages: [...prev.messages, assistantMessage],
        isLoading: false
      }))

    } catch (error) {
      console.error('Error sending message:', error)

      const errorMessage: Message = {
        role: "assistant",
        content: `Sorry, I encountered an error: ${error instanceof Error ? error.message : 'Unknown error'}. Please try again.`,
        timestamp: new Date().toISOString(),
        isError: true
      }

      setChatState(prev => ({
        ...prev,
        messages: [...prev.messages, errorMessage],
        isLoading: false,
        error: error instanceof Error ? error.message : 'Unknown error'
      }))
    }
  }

  // Handle input changes and smart ship detection
  const handleInputChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value
    const cursorPosition = e.target.selectionStart || 0

    setInputValue(value)

    // Detect if user is typing a potential ship name
    const potentialShip = detectPotentialShipName(value, cursorPosition)

    if (potentialShip && potentialShip.word.length >= 2) {
      setSearchQuery(potentialShip.word)
      setCurrentWordBounds({ startPos: potentialShip.startPos, endPos: potentialShip.endPos })

      // Calculate position for dropdown (dropup)
      const input = inputRef.current
      if (input) {
        const rect = input.getBoundingClientRect()
        setAutocompletePosition({
          top: rect.top + window.scrollY, // Use top for dropup calculation
          left: rect.left + window.scrollX
        })
      }

      setShowAutocomplete(true)
      setSelectedIndex(-1)
    } else {
      setShowAutocomplete(false)
      setSearchQuery("")
      setCurrentWordBounds(null)
    }
  }, [])

  // Handle ship selection
  const selectShip = useCallback((ship: Ship) => {
    if (!currentWordBounds) return

    const { startPos, endPos } = currentWordBounds
    const newText = inputValue.substring(0, startPos) + ship.ship_name + inputValue.substring(endPos)

    setInputValue(newText)
    setShowAutocomplete(false)
    setSelectedIndex(-1)
    setSearchQuery("")
    setCurrentWordBounds(null)

    // Focus and position cursor after the inserted ship name
    setTimeout(() => {
      inputRef.current?.focus()
      const newCursorPosition = startPos + ship.ship_name.length
      inputRef.current?.setSelectionRange(newCursorPosition, newCursorPosition)
    }, 0)
  }, [inputValue, currentWordBounds])

  // Handle keyboard navigation
  const handleKeyDown = useCallback((e: React.KeyboardEvent<HTMLInputElement>) => {
    if (!showAutocomplete || filteredShips.length === 0) {
      if (e.key === 'Enter' && !e.shiftKey) {
        e.preventDefault()
        sendMessage()
      }
      return
    }

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault()
        setSelectedIndex(prev => prev < filteredShips.length - 1 ? prev + 1 : 0)
        break
      case 'ArrowUp':
        e.preventDefault()
        setSelectedIndex(prev => prev > 0 ? prev - 1 : filteredShips.length - 1)
        break
      case 'Enter':
        e.preventDefault()
        if (selectedIndex >= 0 && selectedIndex < filteredShips.length) {
          selectShip(filteredShips[selectedIndex])
        } else if (!e.shiftKey) {
          sendMessage()
        }
        break
      case 'Escape':
        e.preventDefault()
        setShowAutocomplete(false)
        setSelectedIndex(-1)
        break
      case 'Tab':
        if (selectedIndex >= 0 && selectedIndex < filteredShips.length) {
          e.preventDefault()
          selectShip(filteredShips[selectedIndex])
        }
        break
    }
  }, [showAutocomplete, selectedIndex, filteredShips, selectShip, sendMessage])

  // Close autocomplete when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (autocompleteRef.current && !autocompleteRef.current.contains(event.target as Node) &&
        inputRef.current && !inputRef.current.contains(event.target as Node)) {
        setShowAutocomplete(false)
        setSelectedIndex(-1)
      }
    }

    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  // Handle quick actions from welcome screen
  const handleQuickAction = useCallback((action: string) => {
    setInputValue(action)
    setTimeout(() => {
      inputRef.current?.focus()
    }, 0)
  }, [])

  return (
    <div className="bg-gray-50 rounded-xl border border-gray-200 shadow-lg flex-1 flex flex-col relative mx-4 my-4">
      {/* Autocomplete Dropdown (Dropup) */}
      <AutocompleteDropdown
        show={showAutocomplete}
        ships={filteredShips}
        position={autocompletePosition}
        selectedIndex={selectedIndex}
        onSelect={selectShip}
        onMouseEnter={setSelectedIndex}
        forwardRef={autocompleteRef}
      />

      {/* Chat Content */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {chatState.messages.length === 0 ? (
          <WelcomeScreen onQuickAction={handleQuickAction} />
        ) : (
          <div className="flex-1 overflow-y-auto p-6">
            <div className="max-w-4xl mx-auto space-y-6">
              {chatState.messages.map((message, index) => (
                <Message key={index} message={message} />
              ))}

              {chatState.isLoading && (
                <div className="flex gap-4 justify-start">
                  <div className="w-8 h-8 rounded-full bg-blue-100 flex items-center justify-center mt-1">
                    <Bot className="w-4 h-4 text-blue-600" />
                  </div>
                  <div className="bg-gray-100 rounded-lg p-4">
                    <div className="flex items-center gap-2">
                      <Loader2 className="w-4 h-4 animate-spin text-blue-600" />
                      <span className="text-gray-600">Thinking...</span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        )}
      </div>

      {/* Chat Input */}
      <ChatInput
        inputValue={inputValue}
        onChange={handleInputChange}
        onKeyDown={handleKeyDown}
        onSend={sendMessage}
        isLoading={chatState.isLoading}
        forwardRef={inputRef}
      />
    </div>
  )
}